import React, { useState } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import {
    Typography,
    FormControlLabel,
    Switch
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import * as Constants from "../common/Constants";
import { connect } from 'react-redux';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: "column",
        height: "100%",
        justifyContent: "center",
        paddingLeft: "16px"
    },
    greetings: {
        color: theme.palette.primary.textColor,
        width: "100%",
        fontWeight: 600,
        fontSize: "24px"
    },
    firstName: {
        color: theme.palette.primary.textColor,
        fontSize: "72px",
        fontWeight: 600
    },
    lastName: {
        color: theme.palette.primary.main,
        fontSize: "72px",
        fontWeight: 600
    },
    designationDiv: {
        paddingTop: "24px",
    },
    designation: {
        fontSize: "22px",
        fontWeight: 500
    },
    link: {
        color: theme.palette.primary.main,
        fontSize: "24px",
        fontWeight: 500,
        cursor: "pointer",
        "&:hover": {
            textDecoration: "underline"
        }
    },
    spaceBetween: {
        color: theme.palette.primary.main,
        fontSize: "24px",
        fontWeight: 500
    },
    linksDiv: {
        paddingTop: "48px"
    }
}));

const AboutInfo = (props) => {
    const classes = useStyles();
    const [themeType, setThemeType] = useState(props.darkMode ? "dark" : "light");

    return (
        <Typography component="div" className={classes.root}>
            <Typography component="div" className={classes.greetings}>HI THERE! I'M</Typography>
            <Typography component="div">
                <Typography component="span" className={classes.firstName}>{Constants.FIRST_NAME}</Typography>&nbsp;&nbsp;&nbsp;&nbsp;<Typography component="span" className={classes.lastName}>{Constants.LAST_NAME}</Typography>
            </Typography>
            <Typography component="div" className={classes.designationDiv}>
                <Typography component="span" className={classes.designation}>{Constants.DESIGNATION}</Typography>
            </Typography>
            <Typography component="div" className={classes.designationDiv}>
                <Typography component="span">{Constants.OBJECTIVE}</Typography>
            </Typography>
            <Typography component="div" className={classes.linksDiv}>
                <Typography component="span" className={classes.link}>CONTACT ME</Typography><Typography component="span" className={classes.spaceBetween}>&nbsp;&nbsp;&nbsp;&nbsp;-&nbsp;&nbsp;&nbsp;&nbsp;</Typography><Typography component="span" className={classes.link}>DOWNLOAD CV</Typography>
            </Typography>
        </Typography>
    );
}

const mapStateToProps = (state) => {
    return {
        darkMode: state.darkMode
    }
}

export default connect(mapStateToProps)(AboutInfo);
